.h2 {
  font-size: 2em;
  line-height: 120%;
  font-weight: 600;
  text-transform: uppercase;
}

.career {
  padding: 3.4375em 0 5em;
  &__wrap {
    display: flex;
    align-items: start;
    gap: 7.5em;
    margin-top: 3.4375em;
  }
  .tabs-head {
    width: 16.25em;
    flex-shrink: 0;
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625em;
    ._active {
      background: #0B91DB;
      border-color: #0B91DB;
      color: #fff;
    }
  }
  &__content {
    max-width: 40.625em;
  }
  ._tab {
    width: 100%;
    height: 2.8125em;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0.0625em solid #000;
    border-radius: 0.625em;
    padding: 0 1em;
    cursor: pointer;
    span {
      font-size: 0.875em;
      line-height: 120%;
    }
  }
  .h2 {
    color: #343434;
    border-bottom: 0.0625em solid #B4B4B4;
    padding-bottom: 0.3125em;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625em;
    margin-top: 1.875em;
    p {
      font-size: 1em;
      line-height: 120%;
    }
  }
  &__btn {
    width: 100%;
    margin-top: 1.875em;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 1em;
    margin-top: 1.875em;
  }
  &__item {
    &-title {
      background: #F4F4F4;
      border-radius: 0.625em;
      padding: 0.75em 1.125em;
      color: #3F3F3F;
      span {
        font-size: 1.25em;
        line-height: 120%;
        font-weight: 600;
      }
    }
    &-text {
      margin-top: 1em;
      p {
        font-size: 1em;
        line-height: 120%;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .h2 {
    font-size: 1.5em;
  }

  .career {
    padding: 1.5em 0 3em;
    &__wrap {
      gap: 2em;
      margin-top: 2em;
    }
    &__text {
      margin-top: 1em;
    }
    &__btn {
      margin-top: 1em;
    }
    &__items {
      margin-top: 1em;
    }
    &__item {
      &-title {
        span {
          font-size: 1.125em;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .career {
    &__wrap {
      flex-direction: column;
      gap: 2em;
    }
    .tabs-head {
      width: 100%;
    }
    &__content {
      max-width: 100%;
    }
  }
}