.product{
  @include grid100gap(1.5em);
}

.product-info,
.filter-select{
  &__btn{
    width: 1.875em;
    height: 1.875em;
    margin-left: auto;
    flex-shrink: 0;
    border-radius: 50%;
    &:active{
      box-shadow: inset 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    }
    svg{
      width: 100%;
    }
    &:hover{
      background-color: $primary;
      rect{
        opacity: 0;
      }
      path{
        stroke: $white;
      }
    }
  }
}

.product-info{
  &._open{
    .product-info__btn{
      transform: rotate(-180deg);
    }

  }
  &__show{
    cursor: pointer;
    display: flex;
    align-items: center;
    padding: 1.25em;
    border-radius: 0.625em;
    border: 1px solid $gray800;
    &:hover{
      background-color: $success;
      border-color: $success;
      color: $white;
      rect{
        stroke: $white;
      }
      path{
        stroke: $white;
      }
    }
  }
  &__num{
    margin-right: 1.31em;
    font-weight: 600;
    span{
      font-size: 2.25em;
    }
  }
  &__title{
    font-size: 1.5em;
    font-weight: 500;
    line-height: 120%;
    text-transform: uppercase;
  }

  &__hidden{
    display: none;
    padding: 1.5em 0 1.25em;
  }
}

.product-desc{
  display: grid;
  grid-template-columns: 22em calc(100% - 23.875em);
  gap: 1em 1.875em;
  &__img{
    border-radius: .625em;
    img{
      width: 100%;
      display: block;
      border-radius: .625em;
    }
  }
  &__text{
    font-size: 1em;
    line-height: 120%;
    *{
      line-height: inherit;
    }
  }
}

.props-table{
  table{
    width: 100%;
    border-spacing: 0;
  }
  th{
    vertical-align: top;
    background-color: $primary;
    text-align: left;
    font-weight: 600;
    padding: .75em 1em 1em 0;
    color: $white;
    position: relative;
    text-transform: uppercase;
    &:first-child{
      border-top-left-radius: 1em;
    }
    &:last-child{
      border-top-right-radius: 1em;
    }
    span{
      max-width: 18em;
      position: relative;
      z-index: 2;
      display: block;
    }
    &:after{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.40) 0%, rgba(0, 0, 0, 0.40) 100%);
      pointer-events: none;
      border-radius: inherit;
    }
  }
  th,td{
    border: 1px solid $gray800;
    &:first-child{
      padding-left: 3.125em;
      width: 13.375em;
    }
    &:not(:first-child){
      border-left: none;
    }
    &:not(:last-child){
      border-right: none;
    }
    span{
      font-size: .875em;
      line-height: 120%;
    }
  }
  tr{
    &:nth-child(even){
      td{
        background-color: $success;
      }
    }
    &:last-child{
      td{
        &:first-child{
          border-bottom-left-radius: 1em;
        }
        &:last-child{
          border-bottom-right-radius: 1em;
        }
      }
    }
  }
  td{
    border-top: none;
    font-weight: 500;
    height: 2.1875em;
    padding-top: .25em;
    padding-bottom: .25em;
    &:nth-child(2)~td{
      width: 3.25em;
    }
  }
}


@media screen and (max-width: 992px){
  .product-info{
    &__show{
      padding: 1em;
    }
    &__num{
      margin-right: 1em;
      span{
        font-size: 2em;
      }
    }
    &__title{
      font-size: 1.125em;
    }
  }

  .product-desc{
    grid-template-columns: 100%;
    &__img{
      max-width: 25em;
    }
  }

  .props-table{
    width: calc(100% + 1em);
    padding-right: 1em;
    padding-bottom: 1em;
    overflow-x: auto;
    table{
      min-width: 45em;
    }
  }
}


@media screen and (max-width: 576px){
  .props-table {
    width: calc(100% + 2em);
    margin-left: -1em;
    padding-left: 1em;
  }
}
