.mistake {
  background-image: url("../img/mistake-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  color: #fff;
  padding: 19.75em 0 8.25em;
  &__title {
    span {
      font-size: 2.25em;
      line-height: 120%;
      font-weight: 600;
      text-transform: uppercase;
    }
  }
  &__text {
    margin-top: 0.3125em;
    span {
      font-size: 16em;
      line-height: 100%;
      font-weight: 700;
    }
  }
  &__btn {
    border-color: #fff;
    color: #fff;
    margin-top: 0.3125em;
  }
}

.error-page{
  .header{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 5;
  }
  .header-top{
    border-color: $gray300;
    color: $white;
  }
  .header__logo{
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
  }
  .lang{
    &__item{
      color: rgba($white,.5);
      &._active{
        color: $white;
      }
    }
    &__split{
      background-color: rgba($white,.5);
    }
  }
  .burger{
    path{
      fill: $white;
    }
  }
  .header-bottom{
    border-color: $gray300;
  }
  @media screen and (min-width: 993px){
    .nav{
      color: $white;
      .menu-item-has-children:after{
        background-image: url(../img/arrow-bottom3.svg);
      }
    }
  }
}


@media screen and (max-width: 992px) {
  .mistake {
    padding: 6.25em 0;
    &__title {
      span {
        font-size: 1.5em;
      }
    }
    &__text {
      span {
        font-size: 6.25em;
      }
    }
  }
}
