.products-index{
  padding: 9.38em 0 6.31em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 4.5em / 4));
    gap: 1.5em;
  }
  &__info{
    grid-column: span 2;
  }
  &__desc{
    margin-top: 1.25em;
  }
  .category-card{
    margin: 0;
  }
}


.about-index{
  padding: 7.37em 0 7.81em;
  background-color: $white;
  &__content{
    width: 40em;
    max-width: 100%;
  }
  &__desc{
    margin-top: 1.875em;
  }
  &__desc2{
    margin-top: .625em;
  }
  &__nav{
    display: flex;
    margin-top: 1.25em;
  }
}

.main{
  padding: 19.75em 0 13em;
  background: linear-gradient(90deg, #000 0%, #001725 48.5%, #006EB7 100%), #0F0F0F;
  color: $white;
  &__content{
    width: 34em;
    max-width: 100%;
  }
  .h1{
    line-height: 120%;
  }
  &__desc{
    margin-top: .1em;
    font-size: 2.25em;
    line-height: 120%;
    color: $gray300;
  }
  &__nav{
    display: flex;
    margin-top: 1.875em;
  }
  &__btn{
    border: 1px solid $primary;
    &:not(:hover){
      border-color: $white;
      background-color: transparent;
    }
    &:hover{
      background-color: $secondary;
    }
  }
}

.news-index{
  padding: 4.125em 0 3.875em;
  border-radius: .625em;
  margin-top: 4.625em;
  background-color: $white;
  .catalog-index__top{
    align-items: center;
    padding-bottom: 2.25em;
    border-bottom: 1px solid $dark;
  }
}

.first-block{
  position: relative;
  &__img{
    width: 46.125em;
    position: absolute;
    right: 0;
    bottom: 0;
    max-width: 58%;
    background-blend-mode: hard-light;
  }
}


@media screen and (max-width: 992px){
  .products-index{
    padding: 2em 0;
    &__grid{
      grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
      gap: 1em;
      grid-auto-flow: dense;
    }
    &__info{
      grid-column: 1/-1;
      order: -1;
    }
    &__desc{
      margin-top: 1em;
    }
  }

  .about-index{
    padding: 2em 0;
    &__desc{
      margin-top: 1em;
    }
    &__nav{
      margin-top: 1em;
    }
  }

  .main{
    padding: 6em 0 3em;
    &__desc{
      font-size: 1.25em;
    }
  }

  .news-index{
    padding: 2em 0;
    margin-top: 1.5em;
    border-radius: 0;
    .catalog-index__top{
      padding-bottom: 1em;
    }
  }

  .first-block{
    &__img{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .products-index {
    &__grid {
      grid-template-columns: 100%;
      gap: 1.5em;
    }
  }

  .main,
  .about-index{
    &__btn{
      width: 100%;
    }
  }
}
