.service-info{
  margin-top: 3.125em;
  display: grid;
  grid-template-columns: 22em calc(100% - 23.75em);
  gap: 1.75em;
  &__img{
    border-radius: 1em;
    img{
      width: 100%;
      border-radius: inherit;
      display: block;
    }
  }
  &__btn{
    width: 100%;
    margin-top: 1em;
  }
  .text{
    ul,
    p{
      margin-bottom: 1em;
    }
  }
}


@media screen and (max-width: 992px){
  .service-info{
    margin-top: 1.5em;
    grid-template-columns: 100%;
    gap: 1em;
    &__img{
      max-width: 25em;
    }
  }
}


