.career-main {
  padding: 3.4375em 0 6.25em;
  &__form {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 1.5em;
    margin-top: 2.5em;
  }
  &__select {
    width: 22.25em;
  }
  &__search {
    position: relative;
    width: calc(100% - 23.75em);
    input {
      width: 100%;
      height: 3.5em;
      border: 0.0625em solid #D9D9D9;
      border-radius: 0.9375em;
      padding: 0 3.5em 0 1.25em;
      font-size: 0.875em;
      line-height: 120%;
      color: #343434;
    }
    &-icon {
      position: absolute;
      z-index: 2;
      top: 50%;
      right: 1.25em;
      transform: translate(0, -50%);
      width: 1em;
      cursor: pointer;
      img {
        width: 100%;
      }
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.875em;
    margin-top: 2.5em;
  }
  &__btn {
    width: 100%;
    margin-top: 3.75em;
    svg {
      top: 50%;
      transform: translate(0, -50%);
    }
  }
}
.career-main__card {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  gap: 1.25em;
  border: 0.0625em solid #D9D9D9;
  border-radius: 0.625em;
  padding: 1.75em 3.5em;
  &-right {
    width: 16.375em;
  }
  &-btn {
    margin-top: 2em;
  }
  &-date {
    width: 100%;
    height: 3.125em;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #E2E2E2;
    border-radius: 0.625em;
    padding: 0 1em;
    margin-bottom: 1.25em;
    span {
      font-size: 1.25em;
      line-height: 120%;
      color: rgba(63, 63, 63, 0.83);
    }
  }
  &-btn2 {
    svg {
      top: 50%;
      transform: translate(0,-50%);
    }
  }
}


.filter-select{
  position: relative;
  &._open{
    .filter-select__btn{
      transform: translateY(-50%) rotate(-180deg);
    }
    .filter-select__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__label{
    color: #7B7B7B;
    font-size: 1em;
    line-height: 125%;
    display: block;
    margin-bottom: .69em;
  }
  &__show{
    z-index: 3;
    position: relative;
    cursor: pointer;
    input{
      border-radius: .71em;
      border: 1px solid $gray800;
      width: 100%;
      height: 3.57em;
      padding-left: 2em;
      padding-right: 4em;
      color: $black;
      font-size: .875em;
      font-family: $font1;
      cursor: pointer;
      &::placeholder{
        color: $black;
      }
    }
  }
  &__btn{
    width: 1.25em;
    height: 1.25em;
    position: absolute;
    top: 50%;
    right: 1.31em;
    transform: translateY(-50%);
  }
  &__dropdown{
    border: 1px solid $black;
    border-radius: .625em;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #fff;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    z-index: 2;
    padding: 4.31em 1.5em 2.25em 1em;
  }
  &__options{
    max-height: 13.25em;
    overflow-y: auto;
    padding-right: 1.25em;
    &::-webkit-scrollbar {
      width: .875em;
      height: .875em;
    }
    &::-webkit-scrollbar-track {
      background: $gray300;
      border-radius: .625em;
    }
    &::-webkit-scrollbar-thumb {
      background: $gray800;
      border-radius: .625em;
    }
  }
  &__option{
    position: relative;
    input {
      position: absolute;
      opacity: 0;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      z-index: 2;
      cursor: pointer;
      &:hover~label,
      &:checked~label{
        background-color: #F3BE5D;
      }
    }
    label{
      transition: all .3s;
      display: flex;
      align-items: center;
      padding: 0 1em;
      height: 2.57em;
      font-weight: 300;
      font-size: .875em;
      border-radius: .625em;
    }
  }
}


@media screen and (max-width: 992px) {
  .career-main {
    padding: 1.5em 0 3em;
    &__form {
      gap: 1em;
      margin-top: 2em;
    }
    &__select {
      width: 50%;
    }
    &__search {
      width: 50%;
    }
    &__cards {
      margin-top: 2em;
    }
    &__btn {
      margin-top: 2em;
    }
  }
  .career-main__card {
    padding: 1em;
  }
}

@media screen and (max-width: 576px) {
  .career-main {
    &__form {
      flex-direction: column;
    }
    &__select,
    &__search {
      width: 100%;
    }
  }
  .career-main__card {
    flex-direction: column;
    gap: .5em;
    &-right {
      width: 100%;
    }
    &-btn{
      margin-top: 1em;
    }
    &-date{
      margin-bottom: .5em;
    }
    &-btn,
    &-date,
    &-btn2 {
      width: 100%;
    }
  }
}
