.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: .9375em;
    line-height: 140%;
    margin-bottom: .375em;
    span{
      color: $primary;
    }
  }
  input,
  textarea{
    border-radius: 0.625em;
    border: 1px solid $black;
    padding: 0 1em;
    height: 2.625em;
    width: 100%;
    font-size: 1em;
    line-height: 120%;
    background-color: transparent;
    color: #000;
    &::placeholder {
      color: #565656;
    }
    &:focus {
      border-color: $primary;
    }
  }
  textarea{
    padding-top: 1em;
    padding-bottom: 1em;
    height: 8.8125em;
    resize: none;
  }
  &__field{
    position: relative;
  }
  &._error{
    input,
    textarea{
      border-color: $error;
      color: $error;
      &::placeholder{
        color: $error;
      }
    }
    .fg__error-text{
      display: block;
    }
  }
  &__error-text{
    margin-top: .375em;
    color: $primary;
    display: none;
  }
}

.file-input__btn,
.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 1.75em;
    min-height: 1.25em;
    span{
      font-size: 1em;
      line-height: 125%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      border: 1px solid $gray800;
      width: 1.25em;
      height: 1.25em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $primary;
        border-color: $primary;
        background-size: 1em auto;
      }
    }
  }
  label {
    &:before {
      border-radius: .1875em;
      background-image: url(../img/check.svg);
    }
  }
}

.file-input{
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  &__btn {
    margin-right: .625em;
    overflow: hidden;
    height: 1.25em;
    padding: 0 1em;
    border-radius: 0.125em;
    border: 1px solid #565656;
    background-color: $gray300;
    @include center;
    span{
      font-size: .75em;
    }
    input{
      width: 400px;
      left: unset;
      right: 0;
    }
  }
  &__output{
    width: 100%;
    margin-top: .25em;
  }
}


