.news-item {
  padding: 3.4375em 0 9.375em;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 1em;
    border-bottom: 0.0625em solid #3F3F3F;
    padding-bottom: 2.8125em;
  }
  &__date {
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    background: #006EB7;
    border-radius: 0.9375em;
    padding: 1.25em 4.5em;
    span {
      font-size: 1.25em;
      line-height: 120%;
      font-weight: 700;
    }
  }
  &__btn {
    svg {
      top: 50%;
      transform: translate(0, -50%);
    }
  }
  &__text {
    margin-top: 2em;
    img {
      width: 100%;
      border-radius: 0.625em;
      margin-bottom: 2.8125em;
    }
    p {
      font-size: 1.25em;
      line-height: 120%;
      &:not(:last-child) {
        margin-bottom: 1em;
      }
    }
  }
  &__btn {
    margin-top: 2.8125em;
  }
}

@media screen and (max-width: 992px) {
  .news-item {
    padding: 1.5em 0 3em;
    &__top {
      padding-bottom: 1em;
    }
    &__date {
      padding: 0.75em 2em;
      span {
        font-size: 1em;
      }
    }
    &__text {
      img {
        margin-bottom: 1em;
      }
      p {
        font-size: 1em;
      }
    }
    &__btn {
      margin-top: 2em;
    }
  }
}

@media screen and (max-width: 576px) {
  .news-item {
    &__top {
      flex-direction: column;
    }
    &__date {
      width: 100%;
    }
    &__btn {
      width: 100%;
    }
  }
}