.news {
  padding: 3.4375em 0 5em;
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 1em;
    border-bottom: 0.0625em solid #3F3F3F;
    padding-bottom: 2.8125em;
  }
  &__info {
    max-width: 22.3125em;
    width: 100%;
    flex-shrink: 0;
    color: #fff;
    background: #006EB7;
    border-radius: 0.9375em;
    padding: 0.875em 2.5em;
    &-text {
      span {
        font-size: 1.25em;
        line-height: 120%;
      }
    }
    &-link {
      display: block;
      margin-top: 0.3125em;
      &:hover {
        color: #fff;
      }
      span {
        font-size: 1.25em;
        line-height: 120%;
        font-weight: 700;
      }
    }
  }
  &__btn {
    width: 100%;
    margin-top: 3.75em;
    svg {
      top: 50%;
      transform: translate(0, -50%) rotate(135deg);
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 2em;
    margin-top: 2em;
  }
}
.news-card {
  display: flex;
  align-items: stretch;
  gap: 1.75em;
  border-bottom: 0.0625em solid #3F3F3F;
  padding-bottom: 2em;
  &__img {
    width: 28.125em;
    flex-shrink: 0;
    img {
      width: 100%;
      border-radius: 0.9375em;
    }
  }
  &__desc {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 1.25em;
  }
  &__date {
    margin-top: 1.25em;
  }
  &__btn {
    margin-left: auto;
    svg {
      top: 50%;
      transform: translate(0, -50%);
    }
  }
}

@media screen and (max-width: 992px) {
  .news {
    padding: 1.5em 0 3em;
    &__top {
      padding-bottom: 1em;
    }
    &__info {
      max-width: unset;
      width: auto;
      padding: 0.75em 2em;
      &-text,
      &-link {
        span {
          font-size: 1em;
        }
      }
    }
    &__btn {
      margin-top: 2em;
    }
  }
  .news-card {
    gap: 1.25em;
    &__img {
      width: 21.875em;
    }
    &__btn {
      width: 100%;
    }
  }
}

@media screen and (max-width: 576px) {
  .news {
    &__top {
      flex-direction: column;
    }
    &__info {
      width: 100%;
    }
  }
  .news-card {
    flex-direction: column;
    &__img {
      width: 100%;
    }
  }
}