.catalog{
  padding: 3.44em 0 8.5em;
  &__grid{
    display: grid;
    grid-template-columns: 18em calc(100% - 18em);
  }
  .h1{
    max-width: 18.25em;
  }
  &__desc{
    margin-top: .94em;
    max-width: 30em;
    &--full{
      max-width: 100%;
    }
    &--full2{
      max-width: 46em;
    }
  }
  &__top{
    padding-bottom: 1.25em;
    margin-bottom: 1.875em;
    border-bottom: 1px solid #B4B4B4;
    &--no-border{
      border: none;
    }
  }
  &__nav{
    display: flex;
    margin-top: 1.5em;
  }
}

.catalog-nav{
  max-width: 11em;
  position: sticky;
  top: 1em;
  color: $dark;
  &__group{
    margin-bottom: 1.25em;
  }
  &__link{
    font-size: 1em;
    font-weight: 600;
    line-height: 120%;
    &._active{
      color: $primary;
    }
  }
  &__submenu{
    margin-top: 1.25em;
    @include grid100gap(.5em);
    a{
      font-size: 0.875em;
      font-weight: 300;
      line-height: 120%;
    }
    ._active{
      font-weight: 600;
      color: $primary;
    }
    ul{
      margin-top: .25em;
      @include grid100gap(.3125em);
      a{
        font-size: 0.75em;
      }
    }
  }
}

.categories{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 1em));
  gap: 1.5em;
  &__link{
    grid-column: span 2;
    height: 10.125em;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    padding: 1.25em 2.1875em;
    border-radius: 1em;
    font-weight: 600;
    img{
      margin-bottom: .125em;
      margin-left: .8125em;
      width: 1.125em;
    }
  }
}

.category-card{
  display: block;
  position: relative;
  padding-bottom: 97.7%;
  border-radius: 1em;
  background-color: #C1C1C1;
  color: $white;
  margin-bottom: 1em;
  &:hover{
    color: $black;
    background-color: transparent;
    &:after{
      opacity: 0;
    }
  }
  &:last-child{
    margin-bottom: 0;
  }
  &--small{
    padding-bottom: 62.4%;
  }
  &--big{
    padding-bottom: 166%;
  }
  &--medium{
    padding-bottom: 100%;
  }
  &--big2{
    padding-bottom: 47.2%;
    grid-column: span 2;
  }
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: inherit;
  }
  &__title{
    z-index: 2;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 1.25em 1.875em;
  }
}


@media screen and (max-width: 992px){
  .catalog {
    padding: 1.5em 0 3em;
    &__grid{
      grid-template-columns: 15em calc(100% - 15em);
    }
    &__top{
      margin-bottom: 1.25em;
    }
  }

  .categories{
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
  }

  .category-card{
    font-size: .875em;
  }
}


@media screen and (max-width: 576px){
  .catalog {
    &__grid {
      gap: 2em;
      grid-template-columns: 100%;
    }
    &__col2{
      order: -1;
    }
  }

  .categories{
    grid-template-columns: 100%;
    &__link{
      grid-column: 1/-1;
    }
  }

  .category-card{
    font-size: 1em;
    &--big2{
      grid-column: 1/-1;
      padding-bottom: 100%;
    }
  }
}
