.contacts-main {
  padding: 3.4375em 0 0 0;
  .map{
    margin-top: 6em;
  }
  &__wrap {
    display: flex;
    align-items: stretch;
    gap: 5.9375em;
    margin-top: 2.8125em;
  }
  &__group {
    width: 23.4375em;
    background: #006EB7;
    border: 0.0625em solid #000;
    border-radius: 0.625em;
    padding: 3.125em 2.5em;
    color: #fff;
  }
  &__group2 {
    width: calc(100% - 29.375em);
    border: 0.0625em solid #0B91DB;
    border-radius: 0.625em;
    padding: 2em;
  }
  &__subtitle {
    span {
      font-weight: 700;
      text-transform: uppercase;
    }
  }
  &__location {
    margin-top: 0.75em;
    span {
      font-size: 1em;
      line-height: 120%;
    }
  }
  &__email {
    display: block;
    margin-top: 0.3125em;
    &:hover {
      color: #fff;
    }
    span {
      font-size: 1em;
      line-height: 120%;
    }
  }
  &__numbers {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625em;
    margin-top: 1.25em;
  }
  &__phone {
    display: block;
    font-weight: 600;
    &:hover {
      color: #fff;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 2em / 2));
    gap: 2em;
  }
  &__item {
    display: flex;
    flex-direction: column;
    &-title {
      margin-bottom: auto;
      span {
        font-size: 1em;
        line-height: 120%;
        font-weight: 600;
      }
    }
    &-text {
      margin-top: 1em;
    }
    &-phone,
    &-email {
      display: block;
      span {
        font-size: 0.875em;
        line-height: 120%;
        font-weight: 300;
        word-wrap: break-word;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .contacts-main {
    padding-top: 1.5em;
    .map{
      margin-top: 3em;
    }
    &__wrap {
      gap: 2em;
      margin-top: 2em;
    }
    &__group {
      width: 45%;
      padding: 2em 1.5em;
    }
    &__group2 {
      width: 55%;
      padding: 2em 1.5em;
    }
    &__items {
      grid-template-columns: repeat(2, calc(50% - 1em / 2));
      gap: 1em;
    }
  }
}

@media screen and (max-width: 576px) {
  .contacts-main {
    &__wrap {
      flex-direction: column;
      gap: 1em;
    }
    &__group,
    &__group2 {
      width: 100%;
    }
    &__items {
      grid-template-columns: 100%;
    }
  }
}
