.header{
  padding-top: .625em;
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 5.5em;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
}

.header-top{
  border-bottom: 1px solid $gray300;
  padding: 1em 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.lang{
  display: flex;
  align-items: center;
  &__item{
    color: rgba(0, 0, 0, 0.50);
    font-size: .8125em;
    font-weight: 500;
    &._active{
      color: $primary;
    }
  }
  &__split{
    background: rgba(0, 0, 0, 0.50);
    width: 1px;
    height: .5em;
    margin: 0 .19em;
  }
}

.header-bottom{
  border-bottom: 1px solid $gray300;
  &__inner{
    display: flex;
    align-items: center;
  }
}

.header-search{
  width: 14.5625em;
  position: relative;
  margin-right: 9em;
  &__btn{
    @include center;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: .13em;
    width: 1.6875em;
    svg{
      width: 100%;
    }
  }
  input{
    padding: 0 3em 0 1.75em;
    border: 1px solid $gray300;
    width: 100%;
    height: 2.75em;
    font-size: .75em;
    border-radius: 1.25em;
  }
}

.desktop-burger,
.burger{
  display: none;
}

.nav{
  color: $dark;
  &>ul{
    display: flex;
    align-items: center;
  }
  .menu-item{
    margin-right: 1.81em;
    height: 3.875em;
    display: flex;
    align-items: center;
    position: relative;
    a{

    }
    &-has-children{
      @media screen and (min-width: 993px){
        display: flex;
        align-items: center;
        &:after{
          content: '';
          background-image: url(../img/arrow-bottom2.svg);
          width: .5em;
          height: .5em;
          @include bgImgSize(100%);
          flex-shrink: 0;
          margin-left: .31em;
        }
        .sub-menu{
          position: absolute;
          top: 100%;
          left: 50%;
          transform: translateX(-50%);
          margin-top: 1px;
          transition: all .3s;
          opacity: 0;
          pointer-events: none;
          width: 8.5em;
          padding-right: .5em;
          border-radius: 0 0 0.625em 0.625em;
          border: 1px solid $gray300;
          border-top: none;
          background: $white;
          padding: .69em 1.25em 1.44em;
        }
        &:hover{
          .sub-menu{
            opacity: 1;
            pointer-events: unset;
          }
        }
      }
    }
  }
  a{
    font-size: .875em;
  }
}

.sub-menu{
  @include grid100gap(.625em);
  a{
    font-size: 0.8125em;
    line-height: 120%;
  }
}

.desktop-burger{
  align-items: center;
  height: 2.8125em;
  border-radius: .625em;
  padding: 0 1em;
  margin-right: .75em;
  color: $white;
  border: 1px solid $white;
  &__icon{
    width: 2.0625em;
    position: relative;
    margin-right: .5em;
    svg{
      @include centerAbsolute;
      width: 100%;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
      width: 70%;
    }
  }
  &__title{

  }
}


@media screen and (max-width: 992px){
  .header{
    padding-top: 0;
    &__logo{
      margin-right: auto;
      z-index: 11;
      position: relative;
    }
    &._open{
      .header-bottom{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .header-top{
    &__inner{
      justify-content: flex-end;
    }
  }

  .header-bottom{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $white;
    padding: 4.75em 0 2em;
    z-index: 10;
    overflow-y: auto;
    display: block;
    &__inner{
      display: block;
    }
  }

  .burger{
    z-index: 11;
    position: relative;
    width: 1.5em;
    height: 1.5em;
    display: flex;
    margin-left: 1em;
    svg{
      @include centerAbsolute;
      width: 100%;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
      width: 90%;
    }
  }

  .header-search{
    margin: 0 0 1em;
    width: 100%;
  }

  .nav{
    &>ul{
      @include grid100gap(1em);
    }
    .menu-item{
      height: auto;
      margin: 0;
      display: block;
    }
  }

  .sub-menu{
    padding: .75em 0 0 1em;
  }
}


