.products-categories,
.catalog-items{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 1em));
  gap: 2.5em 1.5em;
  grid-auto-flow: dense;
  &--col2{
    grid-template-columns: repeat(2,calc(50% - .75em));
    max-width: 46em;
    @media screen and (min-width: 993px){
      .catalog-card__img{
        padding-bottom: 48.2%;
      }
    }
  }
}

.catalog-card{
  &__img{
    @include imgCover(65.8%);
    border-radius: 1em;
  }
  &__btn{
    width: 100%;
    height: 2.25em;
    margin: .625em 0;
    span{
      font-size: .875em;
    }
    img{
      width: 1.125em;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}

.products-categories{
  row-gap: 1.875em;
  .category-card{
    margin: 0;
  }
}


@media screen and (max-width: 992px){
  .products-categories,
  .catalog-items{
    grid-template-columns: repeat(2,calc(50% - .75em));
  }
}


@media screen and (max-width: 576px){
  .products-categories,
  .catalog-items{
    grid-template-columns: 100%;
    gap: 1.5em 1em;
  }
}
