.map{
  height: 48em;
}

.contacts{
  overflow: hidden;
  margin-top: 2.69em;
  position: relative;
  border-radius: .625em .625em 0 0;
  background: rgba($white, 0.90);
}

.contacts-info{
  color: $white;
  position: absolute;
  top: 7.38em;
  right: 14.31em;
  width: 24.625em;
  border-radius: 1em;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.20) 0%, rgba(0, 0, 0, 0.20) 100%), $primary;
  padding: 1.94em 2.56em;
  z-index: 2;
  @include grid100gap(1.19em);
  &__title{
    color: $black;
    @include center;
    border-radius: 0.625em;
    height: 2.6875em;
    background-color: $white;
  }
  &__item{
    @include grid100gap(.31em);
    b{
      font-weight: 600;
    }
  }
  &__phone{
    margin-top: -.625em;
  }
  a{
    &:hover{
      color: $white;
      opacity: .8;
    }
  }
}

.index-page,
.error-page{
  .header._open{
    .header__logo{
      ._view1{
        display: block;
      }
      ._view2{
        display: none;
      }
    }
    .burger{
      path{
        fill: $black;
      }
    }
  }
}

.index-page{
  background-color: #F4F4F4;
  .header{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 5;
  }
  .header-top{
    border: none;
    color: $white;
  }
  .header__logo{
    width: 9.25em;
    ._view1{
      display: none;
    }
    ._view2{
      display: block;
    }
  }
  .lang{
    height: 2.8125em;
    border: 1px solid $white;
    padding: 0 .5em;
    border-radius: .625em;
    &__item{
      color: rgba($white,.5);
      &._active{
        color: $white;
      }
    }
    &__split{
      background-color: rgba($white,.5);
    }
  }
  .burger{
    path{
      fill: $white;
    }
  }
  @media screen and (min-width: 993px){
    .header-top{
      border: none;
      padding-top: 2.5em;
    }
    .header-bottom{
      border-radius: .625em;
      background-color: $white;
      position: absolute;
      width: 100%;
      top: 100%;
      left: 0;
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
      border: 1px solid $black;
    }
    .desktop-burger{
      display: flex;
    }
    .header._open-desktop{
      .header-bottom{
        opacity: 1;
        pointer-events: unset;
      }
      .desktop-burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }
}

.catalog-index{
  padding: 6.19em 0 6.31em;
  background-color: $white;
  &__top{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  .h1{
    max-width: 16.5em;
  }
  &__btn{
    margin-left: 1em;
  }
  &__grid{
    margin-top: 3.81em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .9375em));
    gap: 1.25em;
  }
}


@media screen and (max-width: 992px){
  .contacts{
    margin-top: 1.5em;
    border-radius: 0;
  }

  .contacts-info{
    position: static;
    width: 100%;
    padding: 1em;
    border-radius: 0;
  }

  .map{
    height: 25em;
  }

  .catalog-index{
    padding: 2em 0;
    &__grid{
      margin-top: 1.5em;
      grid-template-columns: repeat(2,calc(50% - .625em));
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-index{
    &__top{
      display: block;
    }
    &__btn{
      width: 100%;
      margin: 1em 0 0;
    }
  }

  .catalog-index{
    &__grid{
      grid-template-columns: 100%;
      gap: 1em;
    }
  }
}
