.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .5;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 39.25em;
        max-width: 100%;
        position: relative;
        z-index: 3;
        display: inline-block;
        padding: 3.88em 5.625em 5.69em;
    }
    &__title{
        font-size: 2.25em;
        font-weight: 600;
        text-transform: uppercase;
        color: $gray800;
    }
    .close-btn{
        position: absolute;
        top: 1.25em;
        right: 1.25em;
    }
}

.close-btn{
    width: 1.0625em;
    @include center;
    svg{
        width: 100%;
    }
    &:hover{
        path{
            fill: $primary;
        }
    }
}

.modal-form{
    margin-top: 2.44em;
    &__fields{
        @include grid100gap(.81em);
    }
    .m-check{
        margin-top: 1em;
    }
    .file-input{
        margin-top: 1.44em;
        margin-bottom: -.5em;
    }
    &__btn{
        margin-top: 1.5em;
        width: 14.5625em;
    }
    &__message{
        display: none;
        align-items: center;
        font-weight: 300;
        margin-top: 1.44em;
        img{
            width: 1.5em;
            margin-left: .44em;
        }
    }
    &._success{
        .modal-form__message{
            display: flex;
        }
        .modal-form__btn{
            display: none;
        }
        .modal-form__hint .gray-text800{
            color: $gray300;
        }
        .file-input{
            color: #C1C1C1;
            &__btn{
                background: $gray300;
                color: #C1C1C1;
                border-color: #C1C1C1;
            }
        }
        .m-check{
            color: $gray300;
            label{
                &:before{
                    border-color: $gray300;
                }
            }
        }
        .fg{
            input,
            textarea{
                border-color: $gray300;
                &::placeholder{
                    color: $gray300;
                }
            }
        }
    }
}

.cookie-modal{
    width: 65.5em;
    max-width: 100%;
    left: 50%;
    transform: translateX(-50%);
    bottom: 1em;
    background: #fff;
    z-index: 3;
    display: inline-block;
    padding: 2.25em 3.625em;
    position: fixed;
    box-shadow: 0 1px 10px rgba(0,0,0,.05);
    border-radius: .625em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    &._active{
        opacity: 1;
        pointer-events: unset;
    }
    .close-btn{
        position: absolute;
        top: 2.5em;
        right: 2.5em;
    }
    .text{
        margin-top: 1.06em;
        p{
            margin-bottom: .625em;
        }
    }
    &__nav{
        display: flex;
        margin-top: .625em;
    }
    &__btn{
        height: 2.25em;
        width: 14.625em;
        span{
            font-size: 1em;
        }
    }
}


@media screen and (max-width: 992px){
    .modal{
        &:after{
            display: none;
        }
        &__dialog {
            padding: 3em 1em 2em;
        }
    }

    .modal-form{
        margin-top: 1.5em;
        &__message{
            margin-top: 1em;
        }
    }

    .cookie-modal{
        padding: 3em 1em 2em;
        .close-btn{
            top: 1.25em;
            right: 1.25em;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        padding: 0;
        &__title {
            font-size: 1.75em;
        }
    }

    .modal-form{
        .m-check{
            margin-top: .625em;
        }
        &__btn{
            width: 100%;
            margin-top: .5em;
        }
    }
}
