.advantage-item{
  padding: 1.12em .56em 1.56em;
  border-bottom: 1px solid #C1C1C1;
  display: grid;
  grid-template-columns: 5.3125em calc(100% - 5.3125em);
  max-width: 46em;
  &__num{
    font-size: 2.25em;
    font-weight: 600;
  }
}

.product-desc2{

}

.text{
  ul,
  p{
    line-height: inherit;
    margin-bottom: 1.81em;
    &:last-child{
      margin: 0;
    }
  }
  li{
    line-height: inherit;
  }
  ul{
    @include grid100gap(.125em);
    li{
      &:before{
        content: '';
      }
    }
  }
}

.use-way{
  display: flex;
  flex-wrap: wrap;
  width: calc(100% + 1.5em);
  &__item{
    display: flex;
    align-items: center;
    margin: .625em 1.5em 0 0;
    padding: 0 5em;
    border-radius: 0.625em;
    min-height: 2.4375em;
    background-color: $primary;
    color: $white;
    &--dark{
      background-color: $gray800;
    }
    &--blue2{
      background-color: $secondary;
    }
    &--green{
      background-color: $success;
    }
  }
}


@media screen and (max-width: 992px){
  .advantage-item{
    grid-template-columns: 4em calc(100% - 4em);
    &__num{
      font-size: 2em;
    }
  }

  .use-way{
    width: calc(100% + 1em);
    &__item{
      padding: .25em 1.25em;
      margin-right: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .use-way{
    width: 100%;
    &__item{
      justify-content: center;
      text-align: center;
      margin-right: 0;
      width: 100%;
    }
  }
}
